"use strict";import{arrayify,hexlify}from"@ethersproject/bytes";import{Coder}from"./abstract-coder";export class DynamicBytesCoder extends Coder{constructor(type,localName){super(type,type,localName,!0)}defaultValue(){return"0x"}encode(writer,value){value=arrayify(value);let length=writer.writeValue(value.length);return length+=writer.writeBytes(value),length}decode(reader){return reader.readBytes(reader.readValue().toNumber(),!0)}}export class BytesCoder extends DynamicBytesCoder{constructor(localName){super("bytes",localName)}decode(reader){return reader.coerce(this.name,hexlify(super.decode(reader)))}}