var DASH=/-([a-z])/g,MS=/^Ms/g,cache={};function toUpper(match){return match[1].toUpperCase()}export default function camelCaseProperty(property){if(cache.hasOwnProperty(property))return cache[property];var camelProp=property.replace(DASH,toUpper).replace(MS,"ms");return cache[property]=camelProp,camelProp}