"use strict";import{getAddress}from"@ethersproject/address";export function isCrowdsaleWallet(json){let data=null;try{data=JSON.parse(json)}catch(error){return!1}return data.encseed&&data.ethaddr}export function isKeystoreWallet(json){let data=null;try{data=JSON.parse(json)}catch(error){return!1}return!(!data.version||parseInt(data.version)!==data.version||3!==parseInt(data.version))}export function getJsonWalletAddress(json){if(isCrowdsaleWallet(json))try{return getAddress(JSON.parse(json).ethaddr)}catch(error){return null}if(isKeystoreWallet(json))try{return getAddress(JSON.parse(json).address)}catch(error){return null}return null}