"use strict";import{arrayify,hexlify}from"@ethersproject/bytes";import{toUtf8Bytes,UnicodeNormalizationForm}from"@ethersproject/strings";export function looseArrayify(hexString){return"string"==typeof hexString&&"0x"!==hexString.substring(0,2)&&(hexString="0x"+hexString),arrayify(hexString)}export function zpad(value,length){for(value=String(value);value.length<length;)value="0"+value;return value}export function getPassword(password){return"string"==typeof password?toUtf8Bytes(password,UnicodeNormalizationForm.NFKC):arrayify(password)}export function searchPath(object,path){let currentChild=object;const comps=path.toLowerCase().split("/");for(let i=0;i<comps.length;i++){let matchingChild=null;for(const key in currentChild)if(key.toLowerCase()===comps[i]){matchingChild=currentChild[key];break}if(null===matchingChild)return null;currentChild=matchingChild}return currentChild}export function uuidV4(randomBytes){const bytes=arrayify(randomBytes);bytes[6]=15&bytes[6]|64,bytes[8]=63&bytes[8]|128;const value=hexlify(bytes);return[value.substring(2,10),value.substring(10,14),value.substring(14,18),value.substring(18,22),value.substring(22,34)].join("-")}