"use strict";import{Logger}from"@ethersproject/logger";import{version}from"./_version";let WS=null;try{if(WS=WebSocket,null==WS)throw new Error("inject please")}catch(error){const logger=new Logger(version);WS=function(){logger.throwError("WebSockets not supported in this environment",Logger.errors.UNSUPPORTED_OPERATION,{operation:"new WebSocket()"})}}export{WS as WebSocket};